import { gql, useQuery, useMutation } from '@apollo/client';

export const GetTitle = () => {

    const GetTitle = gql` 
      query GetTitle {
          generalSettings {
              title
              description
          }
      }
    `
  
    const { loading, error, data } = useQuery(GetTitle);
  
    if (loading) return 'Loading...';
    if (error) return `Error! ${error.message}`;
  
    return data.generalSettings
}

export const GetStaticPosts = (post_type) => {

    console.log(post_type)

    const GetStaticPosts = gql `
    {
        posts(where: {categoryName: "HOME"}) {
          nodes {
            id
            title
            content
            date
            categories {
              edges {
                node {
                  name
                  parent {
                    node {
                      id
                    }
                  }
                }
              }
            }
            uri
            status
            link
          }
        }
      }
    `

    const { loading, error, data } = useQuery(GetStaticPosts);
  
    if (loading) return 'Loading...';
    if (error) return `Error! ${error.message}`;
  
    //console.log(typeof(data.posts.nodes))
    
    return data.posts.nodes

}

