import React, { Fragment } from 'react';
import {  TouchableOpacity, View, Text, Image } from 'react-native';
import { Button } from './styled'
import { GetTitle, UpdateTitle, GetStaticPosts, GetPosts } from '../../../graphql/queries'

import './base.css'

import { schoolLogo } from '../../../statics/constants'

export const Schools = (props) => {

    return schoolLogo.map((school, index) => {

        return (
            <View key={index} style={{flexDirection: "column", margin: 20, width: "20%", border: "0 solid red", justifyContent: "center", textAlign: "center" }}>
                <Image  source={{uri: school.link}} alt={school.name} style={{width: 100, height: 100, alignSelf: "center"}} />
                <Text style={{flexWrap: "wrap"}} >{school.name}</Text>
            </View>
        )

    })

}