import React, { Fragment } from 'react';
import {  TouchableOpacity, View, Text } from 'react-native';
import { Button } from './styled'
import { GetTitle, UpdateTitle, GetStaticPosts, GetPosts } from '../../../graphql/queries'

import { Schools } from './schools';

import './base.css'

function Title(props) {
    return (
        <Fragment>
            <Text style={{fontSize: 24, marginBottom: 0, fontFamily: "Righteous", color: "#222"}} className="title title_maori">{props.ma}</Text>
            <Text style={{fontSize: 24, marginBottom: 10, fontFamily: "Righteous", color: "#7D722D"}} className="title title_english">{props.en}</Text>
        </Fragment>
    )
}

function PostButtons(props) {
    return (
        <View style={{width: "50%", flexDirection: "row"}} className="topic_button_grid">
            <Button style={{border: "none", width: 180, height: 100, backgroundColor: "#9A522D", borderRadius: 20, flexDirection: "column", alignItems: "flex-start"}}  className="topic_button topic_button_1">
                <Text style={{fontWeight: "bold"}}>{`${props.ma1}`}</Text>
                <Text style={{fontWeight: "light"}}>{props.en1}</Text>
            </Button>
            <Button style={{border: "none", width: 180, height: 100, backgroundColor: "#C0A16A", borderRadius: 20, flexDirection: "column", alignItems: "flex-start"}}  className="topic_button topic_button_2">
                <Text style={{fontWeight: "bold"}}>{`${props.ma2}`}</Text>
                <Text style={{fontWeight: "light"}}>{props.en2}</Text>
            </Button>
            <Button style={{border: "none", width: 180, height: 100, backgroundColor: "#88C9F1", borderRadius: 20, flexDirection: "column", alignItems: "flex-start"}}  className="topic_button topic_button_3">
                <Text style={{fontWeight: "bold"}}>{`${props.ma3}`}</Text>
                <Text style={{fontWeight: "light"}}>{props.en3}</Text>
            </Button>
        </View>
    )
}

function PostText(props) {

    if ( props.data.length > 0 ) {
        return props.data.map((content, index) => {
            return (
                <View key={index} style={{width: "50%", textAlign: "justify"}}>
                    <Text style={{fontSize: "1.0em"}}>{content}</Text>
                    <br />
                </View>
            )
        })
    } else {
        return <Text>Loading...</Text>
    }

}

function StaticPost(props) {

    let allContent1 = props.content.split('<p>').pop();
    let allContent2 = allContent1.split('</p>').join()
    let allContent3 = allContent2.split('<br><br>')
	
    let t = allContent3[allContent3.length -1].slice(0, -2) //removes the comma at the end.
    
    // Replaces the last index with the one that has no comma at the end.
    allContent3.pop()
    allContent3 = [...allContent3, t]

    console.log(allContent3)
  
    if ( props.en === "Who are we?" ) {
      return (
        <View className="postRow">
          <Title ma={props.ma} en={props.en} />
          <View className="bodyText">

            <PostText data={allContent3} />

          </View>
          <View style={{marginBottom: "30px"}}> 
            <PostButtons en1={"Christ"} ma1={"Karaiti"} en2={"Learning Group"} ma2={"Kāhui Ako"} en3={"Schools"} ma3={"Kura"} />
          </View>
        </View>
      )
    }

    if ( props.en === "Our Schools" ) {
      return (
        <View className="postRow">
          <Title ma={props.ma} en={props.en} />
          <Text style={{fontSize: 20}} >{"From 2018 the following organisations have committed to our Kāhui Ako"}</Text>
          <View className="bodyText" style={{marginBottom: 30, display: "flex", flexWrap: "wrap", flexDirection: "row", width: "70%"}}>

            <Schools />

          </View>
        </View>
      )
    }
  
    return (
      <View className="postRow">
        <Title ma={props.ma} en={props.en} />
        <View className="bodyText">

            <PostText data={allContent3} />

        </View>
      </View>
    )
}
  
export function AllStaticPosts(props) {
  
    let raw_data = GetStaticPosts()
  
    if ( raw_data.length > 0 ) {
  
      if ( raw_data === "Loading..." ) {
        
        return <Text>Loading...</Text>
  
      } else {
  
        let data = raw_data //.sort( compare );
  
        console.log(data)
  
        return data.map((d, index) => {
  
            let titles = d.title.split('|')
  
            let title_ma = titles[0]
            let title_en = titles[1]
  
            return (
              <StaticPost key={index} en={title_en} ma={title_ma} content={d.content} />
            )
  
        })
      }
  
    } 
  
}