import React, {Fragment} from 'react';
import { TouchableOpacity, View, Text, Image } from 'react-native';
import { NavigationContainer } from '@react-navigation/native';
import { createNativeStackNavigator } from '@react-navigation/native-stack';

import '@expo/match-media'
import { useMediaQuery } from "react-responsive";

import { GetTitle, UpdateTitle, GetStaticPosts, GetPosts } from '../../../graphql/queries'
import { imgs } from '../../../statics/constants'
import { Button, WideButton } from './styled'
import './base.css'

//import navLogo from 'https://wp.nwk.nz/wp-content/uploads/2021/11/logo_transparent_web_sm.png'

const Stack = createNativeStackNavigator();

const config = {
  animation: 'spring',
  config: {
    stiffness: 1000,
    damping: 500,
    mass: 3,
    overshootClamping: true,
    restDisplacementThreshold: 0.01,
    restSpeedThreshold: 0.01,
  },
};

const heroHeight = 500

function HomeScreen({ navigation }) {

  let wp_title = GetTitle()
  let posts = GetPosts()

  if (posts === "Loading...") {

      return <Text>Loading...</Text>

   } else {

      return (
        <View style={{ flex: 1,  alignItems: 'center', justifyContent: 'center' }}>

        <View style={{ position: "absolute",top: 0, flex: 1, overflowY: "hidden"}} >
          <img style={{ height: "300px", width: "100%", overflow: "hidden", justifyContent: "center" , marginTop: 0, filter: "blur(0.2rem)", opacity: 0.7 }} src={imgs[2]} alt="background" />
        </View>
    
        <View style={{ display: "flex", flexDirection: "column", alignItems: "center", justifyContent: "space-around", width: "90%", position: "absolute", top: 0, zIndex: 200}} >
          
          <View style={{ marginTop: 20, flex: 1, backgroundColor: "transparent"}} >
            <img style={{ width: 136, maxWidth: 136, height: 150}} src={imgs[1]} alt="logo" />
          </View>
    
          <View style={{ marginTop: 20, flex: 2, maxWidth: "100%", width: "100%", fontSize: 30}}>
            <span className="righteous" >{wp_title.title}</span>
            <br />
            <span className="righteous" style={{fontSize: 20}} >{wp_title.description}</span>
          </View>
    
          <View style={{marginTop: 20, width: "100%", flexDirection: "row", justifyContent: "space-between"}}>
    
            <TouchableOpacity onPress={() => navigation.navigate('Details')}>
              <Button primary={true}>Who are we?</Button>
            </TouchableOpacity>
    
            <TouchableOpacity onPress={() => navigation.navigate('Details')}>
              <Button>View Workshops</Button>
            </TouchableOpacity>
    
          </View>
    
          <View style={{marginTop: 10, width: "100%", flexDirection: "column", justifyContent: "flex-start"}}>
    
            {posts.map((p, index) => {

              let titles = p.title.split('|')

              return (
                <TouchableOpacity key={index} style={{marginTop: 10}} onPress={() => navigation.navigate('Details')}>
                  <WideButton primary={true}>
                    {`${titles[0]}\n${titles[1]}`}
                  </WideButton>
                </TouchableOpacity>
              )
            })}
    
          </View>
    
        </View>
        </View>
      )

   }

}

function DetailsScreen() {
    return (
      <View style={{ flex: 1, alignItems: 'center', justifyContent: 'center' }}>
        <Text>Details Screen</Text>
      </View>
    );
  }

  const ImageHeader = props => (
    <View style={{ backgroundColor: '#eee' }}>
      <Image
        style={StyleSheet.absoluteFill}
        source={{ uri: 'https://upload.wikimedia.org/wikipedia/commons/3/36/Hopetoun_falls.jpg' }}
      />
      <Header {...props} style={{ backgroundColor: 'transparent' }}/>
    </View>
  );

export function Mobile_Base() {

    let base_title = GetTitle().title ?? ""
    let title2 = base_title.split(' ')

    return (
        <NavigationContainer>
          <Stack.Navigator initialRouteName="Home">
            <Stack.Screen 
              name="Home" 
              component={HomeScreen} 

              options={{ 
                transitionSpec: {
                  open: config,
                  close: config,
                },
                headerTitle: (props) => ( // App Logo
                    <View style={{flexDirection: "row", alignItems: "center"}}>
                        <Image source={{ uri: imgs[0] }} style={{ width: 50, height: 60}} />
                        <Text style={{fontSize: 30, marginLeft: 5, fontFamily: "Righteous"}}>
                            {`${title2[title2.length - 2]} ${title2[title2.length - 1]}`}
                        </Text>
                    </View>
                ),
                headerTitleStyle: { flex: 1, textAlign: 'center', color: "red" },
              }}

            />
            <Stack.Screen 
              name="Details" 
              component={DetailsScreen} 
              options={{ 
                title: 'Details 2',
                transitionSpec: {
                  open: config,
                  close: config,
                },
              }}
            />
          </Stack.Navigator>
        </NavigationContainer>
    );
}